body {
    margin: 0;
    background-color: cadetblue;
    background-image: url(sky.png);
    background-size: contain;
}

.bgHeading {
    /*background-color: grey;*/
    color: palegoldenrod;
}

.bgEven {
    background-color: cadetblue;
}

.bgOdd {
    background-color: grey;
}

.meaningButton {
    float: right;
    background-color: unset;
    border: 0;
    color: black;
    font-weight: 400;
}

.btn-success1:hover, .btn-success1:active, .btn-success1:focus {
    color: unset !important;
    background-color: unset !important;
    border-color: unset !important;
}

.talapatra {
    background-image: url(./slokabg.png);
    background-size: 100% 100%;
    margin-left: 0rem;
    padding-left: 1rem;
}

.cardcss {
    background-color: unset;
    border: 0;
}

.slokaMeaning {
    color: palegoldenrod;
    background-color: unset;
    font-family: Courier, serif;
}

.slokaTranslation {
    color: powderblue;
    background-color: unset;
    font-family: aakar, serif;
}

.custom-toggler .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='palegoldenrod' stroke-width='1' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

.sidebar {
    min-width: 250px;
    max-width: 250px;
    margin-left: 0;
    transition: all 0.5s;
    position: absolute;
}


